import { dataset, getParent } from "@/utils";

export default {
	mounted() {
		// 
		if (this.bindComponentName) {
			const apis = [
				"getForm",
				"setForm",
				"setData",
				"setOptions",
				"toggleItem",
				"hiddenItem",
				"showItem",
				"showLoading",
				"hiddenLoading",
				"resetFields",
				"clearValidate",
				"validateField",
				"validate"
			];

			const parent = getParent.call(this, this.bindComponentName);

			apis.forEach((n) => {
				parent[n] = this[n];
			});
		}
	},

	methods: {
		// 
		_set({ prop, options, hidden, path }, data) {
			let conf = null;

			switch (this.$options._componentTag) {
				case "cl-adv-search":
					conf = this;
					break;
				case "cl-form":
					conf = this.conf;
					break;
				default:
					conf = this.conf;
					break;
			}

			let p = path;

			if (prop) {
				p = `items[prop:${prop}]`;
			}

			if (options) {
				p += `.component.options`;
			}

			if (hidden) {
				p += ".hidden";
			}

			return dataset(conf, p, data);
		},

		// 
		getForm(prop) {
			return prop ? this.form[prop] : this.form;
		},

		// 
		setForm(prop, value) {
			// Add watch
			this.$set(this.form, prop, value);
		},

		// 
		setData(path, value) {
			this._set({ path }, value);
		},

		// 
		setOptions(prop, value) {
			this._set({ options: true, prop }, value);
		},

		// 
		toggleItem(prop, value) {
			if (value === undefined) {
				value = this._set({ prop, hidden: true });
			}

			this._set({ hidden: true, prop }, !value);
		},

		// 
		hiddenItem(...props) {
			props.forEach((prop) => {
				this._set({ hidden: true, prop }, true);
			});
		},

		// 
		showItem(...props) {
			props.forEach((prop) => {
				this._set({ hidden: true, prop }, false);
			});
		},

		// 
		showLoading() {
			this.loading = true;
		},

		// 
		hiddenLoading() {
			this.loading = false;
		},

		// 
		collapseItem(item) {
			// 
			this.clearValidate(item.prop);

			if (item.collapse !== undefined) {
				item.collapse = !item.collapse;
			}
		},

		// 
		resetFields() {
			if (this.$refs["form"]) {
				this.$refs["form"].resetFields();
			}
		},

		// 
		clearValidate(props) {
			if (this.$refs["form"]) {
				return this.$refs["form"].clearValidate(props);
			}
		},

		// 
		validateField(props, callback) {
			if (this.$refs["form"]) {
				this.$refs["form"].validateField(props, callback);
			}
		},

		// 
		validate(callback) {
			if (this.$refs["form"]) {
				this.$refs["form"].validate(callback);
			}
		}
	}
};
